import type React from "react";
import { useState } from "react";
import { SingleInput, TagInput } from "./inputs";
import { useFormik } from "formik";
import { CandidateSearchSchema } from "@/lib/models/candidate";
import { toast } from "react-toastify";
export default function SearchCandidates() {
  const [technologies, setTechnologies] = useState<string[]>([]);
  const [companies, setCompanies] = useState<string[]>([]);
  const [domains, setDomains] = useState<string[]>([]);
  const [preferredLocations, setPreferredLocations] = useState<string[]>([]);
  const [techRole, setTechRole] = useState("");
  const [currentLocation, setCurrentLocation] = useState("");
  const [qualification, setQualification] = useState("");
  const [experience, setExperience] = useState("");
  const [salary, setSalary] = useState("");
  const [noticePeriod, setNoticePeriod] = useState("");

  const formik = useFormik({
    initialValues: {
      Technologies: ["Java"],
      Companies: ["Google"],
      domains: ["IT"],
      preferredLocations: ["Mumbai"],
      techRole: "Java Developer",
      CurrentLocation: "Mumbai",
      qualification: "Graduate",
      experience: "2 years",
      salary: "50000",
      noticePeriod: "1 month",
    },
    validationSchema: CandidateSearchSchema,
    onSubmit: async (values) => {
      console.log(values);
    },
  });

  const handleSearch = () => {
    // Implement search functionality here
    console.log("Searching with current state...");
  };

  return (
    <section className="p-4 space-y-4">
      <form action="" onChange={formik.handleChange}>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          <TagInput
            title="Technologies"
            placeholder="Enter Single or Multiple Technologies"
            tags={formik.values.Technologies}
            onAddTag={(tag) => formik.values.Technologies.push(tag)}
            onRemoveTag={(tag) =>
              formik.values.Technologies.filter((t) => t !== tag)
            }
            showMandatoryButton
          />
          <TagInput
            title="Companies"
            placeholder="Enter Single or Multiple Previous Companies"
            tags={formik.values.Companies}
            onAddTag={(tag) => setCompanies([...companies, tag])}
            onRemoveTag={(tag) =>
              setCompanies(companies.filter((c) => c !== tag))
            }
          />
          <SingleInput
            title="Tech Role"
            placeholder="Enter Tech Role"
            value={formik.values.techRole}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          />
          <SingleInput
            title="Current Location"
            placeholder="Enter Current Location"
            value={formik.values.CurrentLocation}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          />
          <TagInput
            title="Domain"
            placeholder="Enter Single or Multiple Domains"
            tags={formik.values.domains}
            onAddTag={(tag) =>(formik.values.domains.push(tag))}
            onRemoveTag={(tag) => formik.values.domains.filter((d) => d !== tag)}
          />
          <TagInput
            title="Preferred Location"
            placeholder="Enter Single or Multiple Locations"
            tags={formik.values.preferredLocations}
            onAddTag={(tag) =>
              formik.values.preferredLocations.push(tag)
            }
            onRemoveTag={(tag) =>
              formik.values.preferredLocations.filter(
                (l) => l !== tag
              )
            }
          />
          <SingleInput
            title="Qualification"
            placeholder="Enter Qualification Details"
            value={formik.values.qualification}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          />
          <SingleInput
            title="Experience"
            placeholder="Enter Minimum Experience"
            value={formik.values.experience}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          />
          <SingleInput
            title="Salary"
            placeholder="Enter Minimum Salary"
            value={formik.values.salary}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          />
          <SingleInput
            title="Notice Period"
            placeholder="Enter Minimum Days"
            value={formik.values.noticePeriod}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          />
        </div>
        <div className="flex justify-end">
          <button
            className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded"
            onClick={handleSearch} type="submit" disabled={!formik.isValid}
          >
            Search Candidates
          </button>
        </div>
      </form>
    </section>
  );
}
